.link {
  font-family: 'Bangers', cursive; }

.menu {
  padding-top: 40px;
  width: 100%;
  display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between;
  align-items: center; }

.logo {
  background-image: url(./images/logo-w-text.png);
  background-size: contain;
  display: block;
  width: 393px;
  height: 120px;
  text-indent: -9999px; }

.links {
  display: flex;
  font-size: 16px; }

.link {
  color: #545454;
  margin: 5px 10px;
  font-size: 1.5em;
  font-weight: 400;
  line-height: 1.2em; }

@media (max-width: 1200px) {
  .menu {
    padding-top: 20px; }
  .logo {
    width: 240px;
    height: 73px; }
  .links {
    display: none; } }
