.bubble {
  position: relative;
  padding: 10px;
  color: #222;
  border-radius: 10px;
  z-index: -10;
  box-sizing: border-box; }

.text {
  font-family: 'Bangers', cursive;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 19px;
  text-align: center;
  margin-top: -5px;
  color: #e75047;
  padding: 17px;
  box-sizing: border-box; }

.talk_bubble {
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  position: absolute;
  z-index: -11;
  -webkit-filter: drop-shadow(5px 5px 5px #0000001f);
  filter: drop-shadow(5px 5px 5px #0000001f); }
