.paper {
  position: relative;
  min-height: 20px;
  background-color: #fafafa;
  border: 1px solid #fafafa;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(128, 128, 128, 0.1) inset; }
  .paper:before, .paper:after {
    content: "";
    position: absolute;
    z-index: -2; }
  .paper.paper_vintage {
    background-color: #faf8f0;
    border: 1px solid #faf8f0;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(128, 117, 64, 0.1) inset; }

.paper_curl {
  border-radius: 0 0 120px 120px / 0 0 6px 6px; }
  .paper_curl:before, .paper_curl_left:before,
  .paper_curl_right:before, .paper_curl:after, .paper_curl_left:after,
  .paper_curl_right:after {
    bottom: 12px;
    width: 50%;
    height: 55%;
    max-width: 200px;
    max-height: 100px;
    box-shadow: 0 8px 14px rgba(31, 31, 31, 0.5); }
  .paper_curl:before, .paper_curl_left:before,
  .paper_curl_right:before {
    left: 6px;
    transform: skew(-8deg);
    transform: rotate(-3deg); }
  .paper_curl:after, .paper_curl_left:after,
  .paper_curl_right:after {
    right: 6px;
    transform: skew(8deg);
    transform: rotate(3deg); }

.paper_curl_left {
  border-radius: 0 0 0 120px / 0 0 0 6px; }

.paper_curl_right {
  border-radius: 0 0 120px 0 / 0 0 6px 0; }
