.text {
  font-family: 'Bangers', cursive; }

.container {
  perspective: 1000px;
  position: relative;
  width: 100%; }

.textContainer {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: 100%;
  padding: 25px;
  text-align: center;
  box-sizing: border-box; }

.text {
  color: #545454;
  font-weight: 400;
  font-size: 1.7em;
  line-height: 1.1em;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden; }
