.viewContainer {
  font-family: 'Montserrat', sans-serif; }

.viewContainer {
  width: 325px;
  height: 550px;
  background: #fff;
  border-radius: 30px;
  box-shadow: 0 16px 32px 0 rgba(143, 43, 16, 0.16), 0 0px 1px 0 rgba(0, 0, 0, 0.12);
  display: flex;
  justify-content: center;
  overflow: hidden; }

.scrollContainer {
  display: flex;
  padding: 25px;
  min-height: 100%;
  box-sizing: border-box; }
