.question {
  font-family: 'Montserrat', sans-serif; }

.category {
  font-family: 'Bebas Neue', sans-serif; }

.header {
  font-size: 1.7em;
  margin: 5px 0 20px;
  font-weight: 400;
  color: #545454;
  transform-style: preserve-3d;
  backface-visibility: hidden;
  align-self: center; }

.container {
  perspective: 1000px;
  width: 100%;
  display: flex;
  align-items: center; }

.ballotContainer {
  margin-bottom: 15px;
  transform-style: preserve-3d;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden; }

.ballotBody {
  padding-bottom: 20px; }

.statement {
  padding: 20px; }

.category {
  color: #ff9f09;
  margin: 10px 1px;
  font-size: 1.3em;
  line-height: 1em;
  font-weight: 600; }

.question {
  font-size: 1.5em;
  line-height: 1.1em;
  font-weight: 600;
  margin: 2px 10px 12px 0;
  color: #545454; }

.vote {
  color: #2e2e2e; }
  .vote > div {
    margin: 0;
    padding: 0; }
  .vote:before {
    content: "";
    position: relative;
    z-index: -1; }

.option {
  padding: 13px 20px;
  margin: 0;
  font-size: 1.2em;
  line-height: 1em;
  display: block;
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  width: 100%;
  text-align: left; }

.option .answered {
  margin-left: 20px; }
