.montserrat {
  font-family: 'Montserrat', sans-serif; }

.opensans {
  font-family: 'Open Sans', sans-serif; }

.bangers, h1 {
  font-family: 'Bangers', cursive; }

.bebasneue {
  font-family: 'Bebas Neue', sans-serif; }

.flex {
  display: flex; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 400; }

h1 {
  font-weight: 400;
  font-size: 80px; }

.wrapper {
  width: 1200px;
  margin: 0 auto; }

@media (max-width: 1200px) {
  .wrapper {
    width: 90%;
    margin: 0 auto; } }
