.header {
  font-size: 1.7em;
  margin: 15px 0 20px;
  font-weight: 400;
  color: #545454;
  transform-style: preserve-3d;
  backface-visibility: hidden; }

.container {
  perspective: 600px;
  width: 100%; }

.listItemContainer {
  transform-style: preserve-3d;
  backface-visibility: hidden;
  margin-bottom: 20px; }

.listItem {
  display: flex; }

.logoContainer {
  min-width: 75px;
  max-width: 75px;
  min-height: 75px;
  max-height: 75px; }

.textContainer {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center; }

.partyName {
  color: #545454;
  font-size: 1em;
  font-weight: 600;
  margin: 2px 0px; }

.voteScoreText {
  font-size: 0.9em;
  margin: 3px 1px; }

.voteScore {
  font-weight: 600; }
