.accordion__body {
  font-family: 'Open Sans', sans-serif; }

.accordion__title h3 {
  font-family: 'Bebas Neue', sans-serif; }

.accordion__body {
  font-size: 1.2em;
  line-height: 1.6em; }

.u-position-absolute {
  position: absolute; }

.u-position-relative {
  position: relative; }

.accordion__item + .accordion__item {
  border-top: 1px solid rgba(0, 0, 0, 0.1); }

.accordion__item--has-icon {
  position: relative; }

.accordion__title {
  color: #444;
  cursor: pointer;
  padding: 15px 0 0;
  text-align: left;
  border: none;
  outline: none; }
  .accordion__title h3 {
    margin: 0;
    font-size: 2.3em;
    line-height: 1em;
    font-weight: 400;
    color: #000000; }
    .accordion__title h3 span {
      background: #fff;
      padding: 3px 5px; }

.accordion__body {
  color: #2e2e2e;
  display: block;
  padding: 10px 50px 30px 5px;
  opacity: 1;
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out; }
  .accordion__body span {
    background: #fff; }
  .accordion__body.accordion__body--hidden {
    opacity: 0;
    padding-bottom: 0;
    height: 0; }

.question-container {
  margin-bottom: 15px; }

.accordion__title > *:last-child,
.accordion__body > *:last-child {
  margin-bottom: 0; }

.accordion__arrow {
  display: inline-block;
  position: relative;
  width: 24px;
  height: 12px;
  position: absolute;
  top: 50%;
  right: 0;
  margin-top: -6px; }

.accordion__arrow::after,
.accordion__arrow::before {
  display: block;
  position: absolute;
  top: 50%;
  width: 10px;
  height: 2px;
  background-color: currentColor;
  content: ''; }

.accordion__arrow::before {
  left: 4px;
  transform: rotate(45deg); }

[aria-expanded='true'] .accordion__arrow::before, [aria-selected='true'] .accordion__arrow::before {
  transform: rotate(-45deg); }

.accordion__arrow::after {
  right: 4px;
  transform: rotate(-45deg); }

[aria-expanded='true'] .accordion__arrow::after, [aria-selected='true'] .accordion__arrow::after {
  transform: rotate(45deg); }

.accordion__arrow::before,
.accordion__arrow::after {
  transition: transform 0.25s ease, -webkit-transform 0.25s ease; }

/* -------------------------------------------------- */
/* ---------------- Animation part ------------------ */
/* -------------------------------------------------- */
@keyframes fadein {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes move-down {
  0% {
    transform: translateY(0); }
  10% {
    transform: translateY(0); }
  20% {
    transform: translateY(5px); }
  30% {
    transform: translateY(0); }
  100% {
    transform: translateY(0); } }

@keyframes move-up {
  0% {
    transform: translateY(0); }
  10% {
    transform: translateY(0); }
  20% {
    transform: translateY(-5px); }
  30% {
    transform: translateY(0); }
  100% {
    transform: translateY(0); } }

.accordion__title--animated:hover .accordion__arrow {
  animation-name: move-down;
  animation-duration: 1.5s; }

.accordion__title--animated[aria-expanded='true']:hover .accordion__arrow {
  animation-name: move-up;
  animation-duration: 1.5s; }
