.outerContainer {
  padding: 40px 0 80px; }

.container {
  padding: 30px 0;
  position: relative; }

.content {
  display: flex;
  justify-content: center; }

.text {
  width: 407px;
  font-family: 'Montserrat', sans-serif;
  font-size: 42px;
  font-weight: 300;
  line-height: 51px;
  padding: 40px;
  margin: 70px 17px;
  color: #333; }

@media (max-width: 1200px) {
  .outerContainer {
    padding: 20px 0 40px; }
  .content {
    flex-direction: column-reverse; }
  .demo {
    display: flex;
    justify-content: center; }
  .text {
    width: 100%;
    padding: 0 10px;
    margin: 0;
    font-size: 26px;
    line-height: 34px;
    height: calc(100vh - 60px);
    box-sizing: border-box; } }
